"use client";

import { motion } from "framer-motion";
import { Clock, Users, CheckCircle2, Plus, XCircle, TrendingUp } from "lucide-react";
import { Card, CardContent, CardHeader, CardTitle } from "@/app/components/ui/card";
import { Button } from "@/app/components/ui/button";
import Link from "next/link";
import type { Voting } from "../types";

interface VotingCardProps {
  voting: Voting;
  groupId: string;
  index?: number;
}

export default function VotingCard({ voting, groupId, index = 0 }: VotingCardProps) {
  const totalVotes = voting.options.reduce((sum, opt) => sum + opt.votes. length, 0);
  
  // TODO: Replace with actual current user ID
  const currentUserId = "current-user-id";
  const userHasVoted = voting.options. some(opt => 
    opt.votes.some(vote => vote.userId === currentUserId)
  );

  const isExpired = voting.endsAt && new Date(voting.endsAt) < new Date();
  const isActiveAndOpen = voting.isActive && !isExpired;

  const leadingOption = voting.options.length > 0
    ? [... voting.options].sort((a, b) => b. votes.length - a.votes. length)[0]
    : null;

  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ delay: index * 0.05 }}
      whileHover={{ y: -4 }}
    >
      <Card className={`overflow-hidden hover:shadow-xl transition-all relative ${
        ! isActiveAndOpen ? 'opacity-75' : ''
      }`}>
        
        {/* Status badge */}
        <div className="absolute top-4 right-4 z-10">
          {isActiveAndOpen ?  (
            <div className="flex items-center gap-1 px-3 py-1 bg-green-500 text-white text-xs font-semibold rounded-full">
              <div className="w-2 h-2 bg-white rounded-full animate-pulse" />
              Aktywne
            </div>
          ) : (
            <div className="flex items-center gap-1 px-3 py-1 bg-gray-500 text-white text-xs font-semibold rounded-full">
              <XCircle className="w-3 h-3" />
              Zako≈Ñczone
            </div>
          )}
        </div>

        <CardHeader className="bg-gradient-to-r from-primary/10 to-secondary/10 pb-4">
          <div className="pr-24">
            <CardTitle className="text-xl mb-2 line-clamp-2">
              {voting.title}
            </CardTitle>
            {voting.description && (
              <p className="text-sm text-muted-foreground line-clamp-2">
                {voting.description}
              </p>
            )}
          </div>
        </CardHeader>

        <CardContent className="pt-6 space-y-4">
          
          {/* Stats */}
          <div className="flex items-center gap-4 text-sm text-muted-foreground">
            <div className="flex items-center gap-2">
              <Users className="w-4 h-4" />
              <span className="font-medium">{totalVotes} g≈Ços√≥w</span>
            </div>
            <div className="flex items-center gap-2">
              <TrendingUp className="w-4 h-4" />
              <span>{voting.options.length} opcji</span>
            </div>
          </div>

          {/* Metadata */}
          <div className="space-y-2 text-sm">
            <div className="flex items-center gap-2 text-muted-foreground">
              <Clock className="w-4 h-4" />
              <span>
                {new Date(voting.createdAt).toLocaleDateString("pl-PL", {
                  day: "numeric",
                  month: "long",
                  year: "numeric",
                })}
              </span>
            </div>
            
            {voting.endsAt && (
              <div className="flex items-center gap-2 text-muted-foreground">
                <Clock className="w-4 h-4" />
                <span>
                  Ko≈Ñczy siƒô: {new Date(voting.endsAt).toLocaleDateString("pl-PL")}
                </span>
              </div>
            )}

            {userHasVoted && (
              <div className="flex items-center gap-2 text-green-600 font-medium">
                <CheckCircle2 className="w-4 h-4" />
                <span>Zag≈Çosowa≈Çe≈õ</span>
              </div>
            )}
          </div>

          {/* Features */}
          <div className="flex flex-wrap gap-2">
            {voting.allowAddingOptions && (
              <div className="flex items-center gap-1 text-xs bg-primary/10 text-primary px-2 py-1 rounded-full">
                <Plus className="w-3 h-3" />
                Mo≈ºna dodawaƒá opcje
              </div>
            )}
            {voting. allowMultipleVotes && (
              <div className="flex items-center gap-1 text-xs bg-secondary/10 text-secondary px-2 py-1 rounded-full">
                Wiele g≈Ços√≥w
              </div>
            )}
          </div>

          {/* Leading option preview */}
          {leadingOption && totalVotes > 0 && (
            <div className="pt-3 border-t">
              <p className="text-xs text-muted-foreground mb-1">Prowadzi:</p>
              <div className="flex items-center justify-between">
                <span className="text-sm font-medium line-clamp-1">
                  {leadingOption.text}
                </span>
                <span className="text-sm font-bold text-primary">
                  {leadingOption.votes.length} g≈Ços√≥w
                </span>
              </div>
            </div>
          )}

          {/* Action button */}
          <Link href={`/groups/${groupId}/voting/${voting.id}`}>
            <Button className="w-full mt-2" variant={isActiveAndOpen ? "default" : "outline"}>
              {userHasVoted ? "Zobacz wyniki" : isActiveAndOpen ? "Zag≈Çosuj" : "Zobacz szczeg√≥≈Çy"}
            </Button>
          </Link>

        </CardContent>
      </Card>
    </motion.div>
  );
}